import React from "react";

import { Row, Col, Input } from "antd";
import classNames from "classnames";
import StakeButton from "./StakeButton/StakeButton";
import styles from "./PlanCard.module.scss";
const PlanCard = (props) => {
  const { info } = props;
  const { key, profit, totalReturn, withdrawTime, period, calculatedReturn } =
    info;
  const blueTheme = key > 3;
  const fontTheme = blueTheme ? styles.blueFont : styles.yellowFont;

  return (
    <div className={styles.planCard}>
      <div
        className={classNames(styles.planBar, { [styles.blueBg]: blueTheme })}
      >
        {`Plan ${key}`}
      </div>
      <div className={styles.profitDetail}>
        <Row gutter={[8, 8]}>
          <Col span={12}>
            <p>Daily Profit</p>
            <span className={fontTheme}>{`${profit / 10}%`}</span>
          </Col>
          <Col span={12}>
            <p>Total Return</p>
            <span className={fontTheme}>{`${totalReturn}%`}</span>
          </Col>
          <Col span={12}>
            <p>Withdraw time</p>
            <span style={{ fontSize: "28px" }} className={fontTheme}>
              {withdrawTime}
            </span>
          </Col>
          <Col span={12}>
            <p>Days</p>
            <span className={fontTheme}>{period}</span>
          </Col>
          <Col span={24}>
            <p>Enter Amount</p>
            <span className={fontTheme}>
              <Input placeholder="" />
            </span>
          </Col>
        </Row>
      </div>

      <div className={styles.getBnb}>
        <p>In 14 Days You will Get </p>
        <span className={fontTheme}>{`${calculatedReturn}`}</span>
      </div>
      <StakeButton
        title="Stake"
        className={blueTheme ? styles.blueBg : styles.yellowBg}
      />
    </div>
  );
};

export default PlanCard;
